.post-navigation {
  position: relative;
  margin: 0 auto 3rem;
  width: 90vw;

  @media #{$mediumUp} {
    position: absolute;
    margin: auto;
    top: 35vh;
    width: 100%;
  }
}

.nav-previous,
.nav-next {
  border: 1px solid rgb(128, 128, 128);
  border-radius: 10%;

  a {
    display: inline-block;
    padding: 0.625rem;
    width: 100%;
    height: 100%;
  }
}
